$white: #fff;
$primary: hsl(180, 29%, 50%);
$gray-bg: hsl(180, 52%, 96%);
$gray-filter: hsl(180, 31%, 95%);
$gray-cyan: hsl(180, 8%, 52%);
$gray-dark-cyan: hsl(180, 14%, 20%);

$font: "Spartan", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
  "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue";

$font-size-body: 15px;
$font-size-body-mobile: 14px;

$font-weight-bold: 700;

$container-width: 1160px;

$shadow: 1px 5px 18px -2px transparentize($primary, 0.65);

$breakpoint-mobile: 998px;

body {
  background-color: $gray-bg;
  font-family: $font;
  font-size: $font-size-body;
  @media (max-width: $breakpoint-mobile) {
    font-size: $font-size-body-mobile;
  }
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.header {
  height: 152px;
  background-color: $primary;
  background-image: url("../assets//ui/bg-header-desktop.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  @media (max-width: $breakpoint-mobile) {
    // background-position: right center;
    background-image: url("../assets//ui/bg-header-mobile.svg");
  }
}

@import "components/jobs";
@import "components/filter";
